.video {
  padding-top: 121px;
  background: linear-gradient(to bottom, $color-bg-dark 87.8%, $color-white 87.8%);

  @include viewport--md {
    padding-top: 64px;
    background: linear-gradient(to bottom, $color-bg-dark 83%, $color-white 83%);
  }

  @include viewport--sm {
    padding-top: 78px;
    background: linear-gradient(to bottom, $color-bg-dark 88.8%, $color-white 88.8%);
  }

  &__content {
    @include zero;
    width: 25%;
    margin: auto;
    margin-top: 20px;
    font-weight: 300;
    font-size: 16px;
    line-height: 28px;
    text-align: center;
    letter-spacing: -0.005em;
    color: rgba($color-white, 0.7);

    @include viewport--md {
      width: 60%;
      margin-top: 15px;
    }

    @include viewport--sm {
      width: 100%;
    }
  }

  &__content-box {
    padding: 0 10px;
  }

  &__img-box {
    position: relative;
    width: 64.5%;
    margin: auto;
    margin-top: 55px;

    @include viewport--md {
      width: 65.5%;
      margin-top: 40px;
    }

    @include viewport--sm {
      width: 90%;
      margin-top: 53px;
    }
  }

  img {
    display: block;
    margin: auto;
  }

  &__play-box {
    position: absolute;
    display: flex;
    justify-content: center;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
  }

  &__play {
    @include zero;
    position: relative;

    align-self: center;
    width: 65px;
    height: 65px;
    border: none;
    background-color: $color-accent;
    border-radius: 50%;
    outline: none;
    cursor: pointer;
    transition-duration: 0.3s;

    @include viewport--md {
      width: 90px;
      height: 90px;
    }

    @include viewport--sm {
      width: 65px;
      height: 65px;
    }

    svg {
      position: absolute;
      top: 24px;
      left: 28px;
      width: 15px;
      height: 20px;

      @include viewport--md {
        top: 31px;
        left: 33px;
        width: 27px;
        height: 28px;
      }

      @include viewport--sm {
        top: 22px;
        left: 28px;
        width: 15px;
        height: 20px;
      }
    }

    &:hover,
    &:focus {
      background-color: $color-white;
    }

    &:active {
      opacity: 0.7;
    }
  }
}
